
html,
body {
  background-color: #fff;
  color: #636b6f;
  font-family: 'Nunito', sans-serif;
  font-weight: 200;
  height: 100vh;
  margin: 0;
}
a {
  cursor: pointer;
  color: #3b7eff;
}
.content {
  width: 970px;
  margin: auto;
}
.title {
  font-size: 34px;
}
.links>a {
  color: #3b7eff;
  padding: 0 25px;
  font-size: 13px;
  font-weight: 600;
  letter-spacing: .1rem;
  text-decoration: none;
  text-transform: uppercase;
}
.m-b-md {
  margin-bottom: 30px;
}
.np {
  padding: 0 !important;
}
.p {
  padding: 0 8px;
}
.clear {
  overflow: hidden;
}
[class*="col-"] {
  float: left;
  display: block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 8px;
}
.col-1 {
  width: 10%;
}
.col-2 {
  width: 20%;
}
.col-3 {
  width: 30%;
}
.col-4 {
  width: 40%;
}
.col-5 {
  width: 50%;
}
.col-6 {
  width: 60%;
}
.col-7 {
  width: 70%;
}
.col-8 {
  width: 80%;
}
.col-9 {
  width: 90%;
}
.row {
  overflow: hidden;
  margin-bottom: 15px;
}
.center {
  text-align: center;
}
.right {
  text-align: right;
}
input,
select {
  display: block;
  padding: 6px 12px;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
[type=checkbox] {
  display: inline;
  width: auto;
  cursor: pointer;
}
.label-cursor label {
  cursor: pointer;
}
input::-webkit-input-placeholder {
  color: #ccc;
}
input::-moz-input-placeholder {
  color: #ccc;
}
input::-ms-input-placeholder {
  color: #ccc;
}
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
::-webkit-scrollbar-thumb {
  background-color: rgba(50, 50, 50, 0.25);
  border: 2px solid transparent;
  border-radius: 10px;
  background-clip: padding-box;
}
::-webkit-scrollbar-thumb:hover {
  background-color: rgba(50, 50, 50, 0.5);
}
::-webkit-scrollbar-track {
  background-color: rgba(50, 50, 50, 0.05);
}
#fields>div {
  overflow: hidden;
}
#fields label,
#fields label+span {
  display: block;
  /*width: 50%;*/
  float: left;
}
.pull-right {
  float: right;
}
.pull-left {
  float: left;
}

/**
 * 模板展示相关
 */
.file-contents {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  background-color: #000;
}
.file-head,
.file-body {
  width: 800px;
  margin: 0 auto;
  padding: 15px 20px;
  background-color: #fff;
  max-height: 500px;
  overflow: auto;
}
.file-head {
  margin-top: 60px;
  border-bottom: 1px dashed #999;
}
.file-textarea {
  width: 100%;
  min-height: 400px;
}

/**
 * 注意颜色
 */
.note{ color: #b34916;
}
.mkr-css{
  line-height: 26px;
  font-size: 12px;
  margin-right: 20px;
}
.font-small{
  font-size: 12px;
}
